import React, { PureComponent } from 'react';
import { TextValidator } from 'react-material-ui-form-validator';
import { handleChange } from '../../../shared/handleChange';

class CompanyTaxNumber extends PureComponent {
  constructor(props) {
    super(props);
    this.state = {
      value: '',
    };
    this.handleChange = handleChange.bind(this);
  }
  render() {
    return (
      <TextValidator
        name="companyTaxNumber"
        id="companyTaxNumber"
        required
        onChange={this.handleChange}
        value={this.state.value}
        label=""
        fullWidth
        validators={[
          'required',
          'matchRegexp:^[A-Za-z0-9]*$',
          'maxStringLength:17',
        ]}
        errorMessages={[
          '',
          ' A-Z  0-9',
          ' 17 ',
        ]}
        variant="outlined"
        margin="dense"
      />
    );
  }
}

export default CompanyTaxNumber;
